<template>
  <div class="card-mode">
    <!-- Filter Controls -->
    <div class="filter-container">
      <!-- City Filter -->
      <div class="dropdown">
        <label for="city-select">城市：</label>
        <select id="city-select" v-model="localSelectedCity" @change="updateTownOptions">
          <option value="">全部</option>
          <option v-for="city in cityOptions" :key="city" :value="city">{{ city }}</option>
        </select>
      </div>
      <!-- Town Filter -->
      <div class="dropdown" v-show="townOptions.length > 0">
        <label for="town-select">鄉鎮：</label>
        <select id="town-select" v-model="localSelectedTown">
          <option value="">全部</option>
          <option v-for="town in townOptions" :key="town" :value="town">{{ town }}</option>
        </select>
      </div>
    </div>

    <!-- Restaurant Cards -->
    <div class="card-container">
      <div v-for="item in pagedData" :key="item.ID" class="restaurant-card" @mouseenter="handleMouseEnter(item.ID)" @mouseleave="handleMouseLeave(item.ID)">
        <!-- Restaurant Image -->
        <div class="restaurant-image">
          <img :src="item.PicURL" alt="Restaurant Image">
          <!-- Restaurant Information -->
          <div class="restaurant-info">
            <div class="restaurant-description">
              <div class="location-info">
                <div class="city-label">{{ item.City }}</div>
                <div class="town-label">{{ item.Town }}</div>
              </div>
              <div class="restaurant-name" :style="{ color: item.Url ? 'rgb(12, 164, 211)' : 'white' }" @click="openUrl(item.Url)">
                {{ item.Name }}
              </div>
              <div class="description-text" v-show="visibleDescriptions.has(item.ID)">
                {{ truncateText(item.FoodFeature, 25) }} <!-- 限制顯示字數為 50 -->
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Pagination -->
    <div class="pagination">
      <!-- Page Info -->
      <div class="page-info">美食頁次 {{ currentPage }} / {{ totalPages }}</div>
      <!-- Page Buttons -->
      <div class="page-buttons">
        <button v-for="pageNumber in pageNumbers" :key="pageNumber" @click="gotoPage(pageNumber)" :disabled="currentPage === pageNumber" class="page-button" :class="{ active: currentPage === pageNumber }">
          {{ pageNumber }}
        </button>
      </div>
    </div>
  </div>
</template>




<script lang="js">
import axios from 'axios';

export default {
  name: 'CardMode',

  data() {
    return {
      allData: [], // 存儲從 API 獲取的所有數據
      cityOptions: [], // 城市選項
      townOptions: [], // 鄉鎮選項
      localSelectedCity: '', // 當前選擇的城市
      localSelectedTown: '', // 當前選擇的鄉鎮
      visibleDescriptions: new Set(), // 存儲哪些描述應該顯示
      currentPage: 1, // 當前頁碼
      itemsPerPage: 10, // 每頁顯示的項目數
      error: null, // 錯誤信息
      loading: false // 加載狀態
    };
  },

  computed: {
    filteredData() {
      let filtered = this.allData;

      if (this.localSelectedCity) {
        filtered = filtered.filter(item => item.City === this.localSelectedCity);
      }

      if (this.localSelectedTown) {
        filtered = filtered.filter(item => item.Town === this.localSelectedTown);
      }

      return filtered;
    },

    totalPages() {
      return Math.ceil(this.filteredData.length / this.itemsPerPage);
    },

    pagedData() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return this.filteredData.slice(start, end);
    },

    pageNumbers() {
      return Array.from({ length: this.totalPages }, (_, i) => i + 1);
    }
  },

  methods: {
    truncateText(text, length) {
      if (!text) return '';
      return text.length > length ? text.slice(0, length) + '...' : text;
    },

    handleMouseEnter(itemID) {
      this.visibleDescriptions.add(itemID);
    },

    handleMouseLeave(itemID) {
      this.visibleDescriptions.delete(itemID);
    },

    openUrl(url) {
      if (url) {
        window.open(url, '_blank');
      }
    },

    gotoPage(pageNumber) {
      if (pageNumber > 0 && pageNumber <= this.totalPages) {
        this.currentPage = pageNumber;
      }
    },

    updateCityOptions() {
      this.cityOptions = [...new Set(this.allData.map(item => item.City))].sort();
      this.updateTownOptions();
    },

    updateTownOptions() {
      if (this.localSelectedCity) {
        this.townOptions = [...new Set(this.allData.filter(item => item.City === this.localSelectedCity).map(item => item.Town))].sort();
      } else {
        this.townOptions = [];
      }
    },

    async fetchData() {
      try {
        this.loading = true;
        const response = await axios.get('https://data.moa.gov.tw/Service/OpenData/ODwsv/ODwsvTravelFood.aspx');
        this.allData = response.data;
        this.updateCityOptions();
      } catch (error) {
        this.error = '資料取得錯誤，請稍後重試。';
      } finally {
        this.loading = false;
      }
    }
  },

  mounted() {
    this.fetchData();
  }
};
</script>




<style lang="scss" scoped>
.card-mode {
  display: flex;
  flex-direction: column;
}

.filter-container {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.dropdown {
  margin-bottom: 10px;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.restaurant-card {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
  border: 1px solid #ddd;
  border-radius: 12px;
  width: 300px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  background-color: #fff;
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

.restaurant-card:hover {
  transform: scale(1.3);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.restaurant-image {
  width: 100%;
  height: 180px;
  overflow: hidden;
  position: relative;
}

.restaurant-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.restaurant-info {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 10px;
  background: rgba(0, 0, 0, 0.5);
  color: white;
  text-align: left;
  transition: opacity 0.3s ease;
}

.restaurant-description {
  display: flex;
  flex-direction: column;
}

.description-text {
  display: -webkit-box;
  -webkit-line-clamp: 2; /* 限制顯示兩行 */
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-top: 10px;
  font-size: 14px;
  line-height: 1.4em; /* 控制行高 */
  max-height: 2.8em; /* 控制最大高度，與行高匹配 */
  transition: max-height 0.3s ease;
}

.restaurant-card:hover .description-text {
  -webkit-line-clamp: unset; /* 懸停時顯示完整內容 */
  overflow: visible;
  text-overflow: clip;
  max-height: none; /* 取消最大高度限制 */
}


.restaurant-name {
  font-weight: bold;
  font-size: 16px;
  color: white;
  cursor: pointer;
  transition: color 0.3s ease;
}

.restaurant-name:hover {
  color: rgb(12, 164, 211);
}

.location-info {
  display: flex;
  gap: 10px;
  margin-bottom: 5px;
}

.city-label {
  display: block;
  background-color: rgb(12, 164, 211);
  color: white;
  padding: 2px 8px;
  border-radius: 4px;
  font-size: 11px;
}

.town-label {
  display: block;
  color: #ddd;
  font-size: 11px;
}

.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}

.page-info {
  margin-right: 10px;
}

.page-buttons {
  display: flex;
}

.page-button {
  margin: 0 5px;
  padding: 5px 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: white;
  color: black;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.page-button.active,
.page-button:hover {
  background-color: rgb(12, 164, 211);
  color: white;
}

.page-button:disabled {
  background-color: #f5f5f5;
  cursor: not-allowed;
}
</style>
